em {
  background: cyan;
  font-style: normal;
}
.App{
  display: inline-flex;
  flex-direction: column;
  width: 100vw
  
}
.header {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 50px;
  padding: 0.5rem 1rem;
  background-image: linear-gradient(to right, #0000ff, black,  #ff0803 );
  color: #fff;
  margin-bottom: 1rem;
  width: 100%
}


.header a {
  color: #fff;
  text-decoration: none;
}

.header-title {
  font-size: 1.6rem;
  font-weight: bolder;
  
}
.searchbox{
  display: inline-flex;
  width: 80%
}

.ais-SearchBox searchbox{
  width: 80%
}
.header-title::after {
  padding: 0 0.5rem;
}

.header-subtitle {
  font-size: 1.2rem;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 1rem;
  display: inline-flex;
  width: 100%
  
  
  
}

.ais-Hits-list{
  max-width: 100%;
}

.search-panel {
  display: flex;

}



.left-panel {
  display: inline-flex;
  width: 500px;
  min-width: 100px;
  flex-direction: column;
  margin: 25px;

}
.search-panel__filters {
  flex: 1;
}

.refinement-clear{
  margin-bottom: 20px
}

.search-panel__results {
  flex: 3;
}

.searchbox {
  margin-bottom: 2rem;
}

.pagination {

    margin: auto;
    margin-top: 45px;
    text-align: center;
    width: fit-content;
  

  text-align: center;
}

.ais-Hits-item:hover {
background-color: #9bd4f2;
}
 

.hit-preview{
  height: 100%
}
  
.ais-Hits-item, .ais-InfiniteHits-item, .ais-InfiniteResults-item, .ais-Results-item{
  min-width: 200px;
}

.ais-RefinementList-list{
  max-height: 400px;
  overflow-y: scroll;
  
}
